.sidebarMenuItem {
  height: 100vh;
  border-right: 1px solid #eee;
  position: sticky;
  top: 0;
  left: 0;
}

.order_datas p {
  word-wrap: break-word;
}

.show_profile {
  /* background-color: #eeee; */
  /* background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%); */
  background-image: linear-gradient(to top, #e6e9f0 0%, #eef1f5 100%);
  padding: .5rem;
  margin: .5rem;
  border-radius: 8px;
}

.profile_img img {
  width: 6rem;
  position: absolute;
}

.ant-menu-light.ant-menu-root.ant-menu-inline {
  border-inline-end: unset !important;
}

.sidebar_title img {
  width: 60%;
  margin-right: 0.5rem;
}

.side_log {
  cursor: pointer;
  background-color: white;
}

.side_sub_menu {
  font-size: 1rem;
}

.side_menu_icon {
  font-size: 1rem !important;
  color: white;
}

.logut_btn {
  position: absolute;
  bottom: 30%;
  left: 12%;
}

.logut_btn button {
  padding: 0 3rem;
}