#profile_img{
    height: 8rem;
    width: 8rem;
    border-radius: 50%;
    object-fit: cover;
   
    z-index: 1;
    padding: 3px;

}
.part_card{
    position: relative;
}
.show_shadow1{
    height: 8rem;
    width: 8rem;
    background-color:var(--primary_color);
    border-radius: 12px;
    position: absolute;
    top: 0;
    right: 0;
    opacity: .1;

}
.show_shadow2{
    height: 8rem;
    width: 8rem;
    background-color:var(--primary_color);
    border-radius: 12px;
    position: absolute;
    top: 5%;
    right: 5%;
    opacity: .1;
    z-index: 2;
}
