.partner_content{
    margin-top: 1rem;
}
.del_part_details button{
    background-color: var(--primary_color);
    color: white;
    border-style: none;
}
.show_title_img img{
    width: 3rem;
    margin-bottom: .5rem;
    margin-right: .5rem;
}
.show_ost_blance .ant-card{
    background-image: linear-gradient(to top, #cfd9df 0%, #e2ebf0 100%);
}
.show_ost_blance .ant-card-body{
    display: flex;
    padding: 12px;
}
.show_title_info p{
    font-weight: 600;
    font-size: 1rem;
}

.profile_cover{
    background-color: #5ac268af;
    border-top-left-radius: 14px !important;
    border-top-right-radius: 14px !important;
    padding-top: 50px !important;
}

.update_vendor{
    display: flex;
    justify-content: end;
}
.update_vendor button{
    background-color: var(--primary_color);
    color: white;
    border-style: none;
}
#live_order{
    color: var(--primary_color);
}