.booking_chart {
    margin-top: 2rem;
}

/* .main_Section{
    margin-top: 1rem;
} */
.show_user_chart {
    margin-top: 1rem;
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;

    .ant-card {
        display: inline-block;
        width: 30%;
        margin-right: 1rem;
        margin-bottom: 1rem;
    }
}

.ant-layout .ant-layout-sider {
    background-color: transparent;
}

.ant-menu-light {
    height: 100vh;
}

.title {
    background-color: white;
}

.main_layout {
    height: 100vh !important;
}

.head_menu {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1rem;
}

.logged_user p {
    font-size: 1.2rem;
    font-weight: 600;
    text-decoration: dashed;
}

.ant-menu-light .ant-menu-item-selected {
    color: var(--primary_color) !important;
}
.logged_user img{
    width: 2rem;
}
.logged_user span {
    color: var(--primary_color);
}

.ant-menu-light.ant-menu-inline .ant-menu-item {
    color: gray;
    font-size: 1rem;
}

.show_user_revanue {
    margin-top: 1rem;
}

.ant-menu-light.ant-menu-inline .ant-menu-item a {
    text-decoration: none;
}

.shoo_recent_order {
    margin-top: 1rem;
}

.shoo_recent_order p {
    font-size: 1.2rem;
    font-weight: 600;
    margin-bottom: 1rem;
}

.ant-menu-light.ant-menu-inline .ant-menu-item .ant-menu-selected {
    /* color: var(--primary_color); */
    background-color: lightgrey;
}

.ant-menu-light .ant-menu-submenu-selected>.ant-menu-submenu-title {
    color: rgb(43, 40, 40);
}

.colorChange {
    background-color: var(--secondry_color);
}

.user_chart_logo img {
    width: 4rem;
    height: 4rem;
    margin-right: 0.5rem;
    background: #eee;
    border-radius: 50%;
    object-fit: contain;
}

.user_data_item {
    position: relative;
    display: flex;
    align-items: center;
}

.count_img {
    width: 50%;
    position: absolute;
    bottom: 0;
    right: 0;
}
.count_img img{
    width: 100%;
}

.show_user_chat .ant-card {
    height: 16vh;
    background-image: linear-gradient(120deg, #fdfbfb 0%, #ebedee 100%);
}

.show_user_chat {
    margin-top: 1rem;
}

.user_chart_text p:first-child {
    font-size: 1.2rem;
    font-weight: 600;

}

.user_chart_text p:last-child {
    font-size: 1.5rem;
    font-weight: 600;
}

.content_head .ant-card {
    width: 100%;

}

.show_card_design .ant-card {
    position: relative;
}

.card_svg {
    position: absolute;
    top: 0;
    left: 0;
}